.menu {
    display: none;
}





.menu h2 {
    text-align: center;
    color: #ddd;
}

.menuImg {
    padding-top: 8px;
    min-width: 200px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.menuImg img {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    border: 2px solid #fff;
}

.menuBtns {
    
    display: flex;
    flex-direction: column;
    align-items: center;

    padding-top: 48px;
}

.menuBtns a {
    font-family: 'Inter';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 24px;
    color: #fff;

    width: 80%;
    margin-bottom: 24px;
    background: #023436;
    border: none;
    padding: 8px;
    box-shadow: 1px 1px 1px #fff;
    border-radius: 8px;
    text-decoration: none;
    text-align: center;
}

.menuBtns a:hover {
    background-color: #03b5aa;
    color: #ddd;
}

.menuBtns .active {
    background-color: #037971;
    color: #ddd;
}

@media (min-width: 768px){
    .menu {
        display: block;
        position: fixed;
        top: 0;
        bottom: 0;
        left: 0;
        background: linear-gradient(180deg, #023436 0%, rgba(11, 124, 124, 0.2) 260%);
        padding: 8px;
    }
    .mob {
        display: none;
    }
}